.align-elements{
	text-align: center;
}

.container{
	width:100%;
}

.button{
	font-family: Helvetica, Arial, sans-serif;
	color:white;
  border: none;
  font-size: 14pt;
  font-weight: bold;
  border-radius: 5px;
}

.login-button{
	width:100%;
	padding: 10px;
}

.demo-button{
  padding: 10px;
  margin-top: 10px;
  width:100%;
  /*ASK MARINA FOR 3 SPECIFIC COLORS TO GRADIENT*/
  background: -webkit-gradient(linear, left top, right top, from(purple), color-stop(green) , to(blue));
  background: -webkit-linear-gradient(left, purple, green , blue);
  background: -o-linear-gradient(left, purple, green , blue);
  background: linear-gradient(to right, purple, green , blue);
}

.demo-button:hover{
  background: -webkit-gradient(linear, left top, right top, from(blue), color-stop(green) , to(purple));
  background: -webkit-linear-gradient(left, blue, green , purple);
  background: -o-linear-gradient(left, blue, green , purple);
  background: linear-gradient(to right, blue, green , purple);
}


